<template>
    <section class="container_toddler">
        <Icon
            @click="decrease"
            class="VectorL"
            icon="VectorL"
            color=" rgba(255, 255, 255, 0.5)"
        />
        <div class="container_toddler_input">
            <p class="toddler_text"><slot></slot></p>
            <input
                class="toddler_input"
                type="range"
                min="1"
                max="100"
                step="10"
                :value="num"
            />
        </div>
        <Icon
            @click="increase"
            class="VectorR"
            icon="VectorR"
            color=" rgba(255, 255, 255, 0.5)"
        />
    </section>
</template>

<script>
import Icon from "./fontsImg/Icon";
export default {
    components: { Icon },
    data() {
        return {
            num: 0,
        };
    },
    methods: {
        increase() {
            this.num += 10;
        },
        decrease() {
            this.num -= 10;
        },
    },
    mounted() {},
};
</script>
